/* eslint-disable no-underscore-dangle */
/* eslint-disable @typescript-eslint/camelcase */
import { GO_TO_CATALOG } from 'mocks/goToCatalog';
import { ITEMS_CHANGES_MOCK } from 'mocks/itemsChanges';
import { SHOW_ITEMS_MOCK } from 'mocks/showItems';
import { createStore as createReduxStore, Middleware, applyMiddleware, compose, StoreEnhancer } from 'redux';
import { routerMiddleware, push, replace, goBack, LOCATION_CHANGE } from 'connected-react-router';
import thunk from 'redux-thunk';
import { createHashHistory } from 'history';
import { applyItemsChanges } from 'store/actions/app';
import { getCountById } from 'store/reducers/helpers/catalogPage';
import { addressFullSuccessThunk, addressFullValidationOkThunk } from 'store/thunks/addressFullPage';
import { goToCatalogThunk, goToValidationThunk, validationOkThunk } from 'store/thunks/native';
import { AppStateInterface } from 'types/app-state';

import { AssistantSdkCommand } from './constants/native';
import { AssistantSdkServerAction } from './constants/server';
import { ROUTES } from './constants/router';
import { AppState, AppBaseAction, AppMiddleware, AppDispatch } from './constants';
import { getRootReducer } from './reducers';
import {
    showItemsAction,
    addItemAction,
    removeAllAction,
    removeItemAction,
    addressInputAction,
    addressInputAddAction,
    addressInputClearAction,
    addressFullInputAddAction,
    addressFullInputClearAction,
    addressFullValidationErrorAction,
    askAgainForOrderStatusFinalAction,
    goBackAction,
    askAgainForOrderStatusAction,
    addressFullInputAction,
    orderErrorAction,
    itemsChangesAction,
    goToWaitAction,
} from './actions/native';
import {
    askForOrderStatusFinalAction,
    askForOrderStatusAction,
    askForStockAction,
    askForItemsChangesAction,
    pageChangeAction,
} from './actions/server';
import { selectPathname } from './selectors';
import { ScenarioUser, ScenarioUserAddress, ScenarioUserLocation, StoreUser } from './constants/user';

const serverActionMiddleware: AppMiddleware = ({ dispatch, getState }) => (next) => (action) => {
    switch (action.type) {
        case 'CLOSE_APP': {
            window.AssistantClient.close();
            break;
        }
        case 'ASK_FOR_STOCK': {
            window.AssistantClient.sendData({ action_id: action.type });

            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: SHOW_ITEMS_MOCK,
                    });
                }, 500);

                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'ADD_ITEM',
                            item: '21ff407a-fa98-11e8-80c5-0cc47a817925',
                            count: '5',
                        },
                    });
                }, 1000);

                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'ADD_ITEM',
                            item: '21ff407c-fa98-11e8-80c5-0cc47a817925',
                            count: '2',
                        },
                    });
                }, 1500);
            }

            break;
        }
        case 'ASK_FOR_ITEMS_CHANGES': {
            window.AssistantClient.sendData({ action_id: action.type });

            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: ITEMS_CHANGES_MOCK,
                    });
                }, 500);
            }

            break;
        }
        case 'ADDRESS_SUBMIT': {
            window.AssistantClient.sendData({ action_id: action.type });
            break;
        }
        case 'ADDRESS_FULL_SUBMIT': {
            window.AssistantClient.sendData({ action_id: action.type });

            //       
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'GO_TO_VALIDATION',
                    },
                });
            }

            break;
        }
        case 'ASK_FOR_ORDER_STATUS': {
            window.AssistantClient.sendData({ action_id: action.type });

            //       
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'ASK_AGAIN_FOR_ORDER_STATUS_FINAL',
                    },
                });
            }

            break;
        }
        case 'ASK_FOR_ORDER_STATUS_FINAL': {
            window.AssistantClient.sendData({ action_id: action.type });

            //       
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'VALIDATION_OK',
                        status: true,
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        order_id: '364528',
                    },
                });
            }

            break;
        }
        case 'MAKE_PURCHASE': {
            window.AssistantClient.sendData({ action_id: action.type });

            //       
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'GO_TO_WAIT',
                        },
                    });
                }, 500);
            }

            break;
        }
        case 'PAGE_CHANGE': {
            window.AssistantClient.sendData({ action_id: action.type });
            break;
        }
        /**
         * tldr;   LOCATION_CHANGE  dispatch'     setTimeout.
         *     action'  LOCATION_CHANGE,   
         *  connected-react-router  history.
         *
         * !           ,
         *      .
         *
         *  ,   ,    . ,   ,
         *      connected-react-router  history.
         * ,      .  
         * dispatch' push action react-connected-router'a   @@router/CALL_HISTORY_METHOD
         *   router middleware  
         *   (push)   history.
         *    history    History API 
         *    .   ,   .
         *  connected-react-router'  ,  
         *    (,  ) history.
         *  dispatch' action,   @@router/LOCATION_CHANGE,   
         *   connected-react-router   (    history)  router'a.
         *            react',
         *   connected-react-routera   ,    ,
         *    router'a      history.
         *     .        .
         *
         * -      connected-react-router'    action,
         *     (redux   ),   
         *    .    ,    -   history
         *      ,   router'a   ,  
         *     action.
         *       history,     
         *            .
         **/
        case LOCATION_CHANGE: {
            const state = getState();

            const currentPathname = selectPathname(state);
            const nextPathname = action.payload.location.pathname;

            switch (currentPathname) {
                case ROUTES.CATALOG:
                case ROUTES.CART: {
                    setTimeout(() => {
                        dispatch(applyItemsChanges());
                    });

                    break;
                }

                default:
                    break;
            }

            switch (nextPathname) {
                case ROUTES.CATALOG: {
                    if (Object.keys(state.productsData.itemsMap).length) {
                        setTimeout(() => {
                            dispatch(askForItemsChangesAction());
                        });
                    } else {
                        setTimeout(() => {
                            dispatch(askForStockAction());
                        });
                    }

                    break;
                }
                case ROUTES.CART: {
                    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                        if (!Object.keys(state.productsData.itemsMap).length) {
                            setTimeout(() => {
                                dispatch(askForStockAction());
                            });
                        }
                    }

                    setTimeout(() => {
                        dispatch(askForItemsChangesAction());
                    });

                    break;
                }

                default:
                    break;
            }

            //     LOCATION_CHANGE,
            //         
            if (currentPathname !== nextPathname) {
                setTimeout(() => {
                    dispatch(pageChangeAction());
                });
            }

            break;
        }
        default:
            break;
    }

    return next(action);
};

export const history = createHashHistory();
const enhancers: StoreEnhancer[] = [];
const middlewares: Middleware[] = [thunk, routerMiddleware(history), serverActionMiddleware as Middleware];

if (process.env.NODE_ENV !== 'production') {
    if (typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION__) {
        enhancers.push(window.__REDUX_DEVTOOLS_EXTENSION__());
    }
}

// TODO:       
// https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-42
export const store = createReduxStore<AppState, AppBaseAction, {}, {}>(
    getRootReducer(history),
    compose(applyMiddleware(...middlewares), ...enhancers),
);
const dispatch = store.dispatch as AppDispatch;

if (process.env.NODE_ENV !== 'production') {
    window.__store = store;
}

export const dispatchToReduxStore = ({ smart_app_data }: AssistantSdkCommand) => {
    switch (smart_app_data.command) {
        case 'SHOW_ITEMS':
            dispatch(showItemsAction(smart_app_data));
            break;
        case 'ITEMS_CHANGES': {
            dispatch(itemsChangesAction(smart_app_data));
            break;
        }
        case 'ADD_ITEM': {
            dispatch(addItemAction(smart_app_data));
            break;
        }
        case 'REMOVE_ALL': {
            dispatch(removeAllAction());
            break;
        }
        case 'REMOVE_ITEM': {
            dispatch(removeItemAction({ ...smart_app_data, shouldRemoveMention: true }));
            break;
        }
        case 'GO_TO_CATALOG': {
            dispatch(goToCatalogThunk(smart_app_data));
            break;
        }
        case 'GO_TO_NO_AREA': {
            dispatch(push(ROUTES.NO_AREA));
            break;
        }
        case 'GO_TO_VALIDATION': {
            dispatch(goToValidationThunk());
            break;
        }
        case 'GO_TO_PROMO': {
            dispatch(push(ROUTES.PROMO));
            break;
        }
        case 'GO_TO_ADDRESS_SIMPLE': {
            dispatch(push(ROUTES.ADDRESS_SIMPLE));
            break;
        }
        case 'GO_TO_ADDRESS_FULL': {
            dispatch(push(ROUTES.ADDRESS_FULL_ALL));
            break;
        }
        case 'VALIDATION_OK': {
            dispatch(validationOkThunk(smart_app_data));
            break;
        }
        case 'ORDER_ERROR': {
            dispatch(orderErrorAction(smart_app_data));
            dispatch(replace(ROUTES.ERROR));
            break;
        }
        case 'ADDRESS_INPUT': {
            dispatch(addressInputAction(smart_app_data));
            dispatch(push(ROUTES.ADDRESS_SIMPLE));
            break;
        }
        case 'ADDRESS_INPUT_ADD': {
            dispatch(addressInputAddAction(smart_app_data));
            break;
        }
        case 'ADDRESS_INPUT_CLEAR': {
            dispatch(addressInputClearAction());
            break;
        }
        case 'ADDRESS_FULL_SUCCESS': {
            dispatch(addressFullSuccessThunk());
            break;
        }
        case 'ADDRESS_FULL_INPUT': {
            dispatch(addressFullInputAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_INPUT_ADD': {
            dispatch(addressFullInputAddAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_INPUT_CLEAR': {
            dispatch(addressFullInputClearAction());
            break;
        }
        case 'ADDRESS_FULL_VALIDATION_OK': {
            dispatch(addressFullValidationOkThunk());
            break;
        }
        case 'ADDRESS_FULL_VALIDATION_ERROR': {
            dispatch(addressFullValidationErrorAction());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS_FINAL': {
            dispatch(askAgainForOrderStatusFinalAction());
            setTimeout(() => dispatch(askForOrderStatusFinalAction()), 500);
            break;
        }
        case 'GO_TO_WAIT': {
            dispatch(push(ROUTES.WAIT));
            dispatch(goToWaitAction());
            dispatch(askForOrderStatusAction());
            break;
        }
        case 'GO_BACK': {
            dispatch(goBackAction());
            dispatch(goBack());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS': {
            dispatch(askAgainForOrderStatusAction());
            setTimeout(() => dispatch(askForOrderStatusAction()), 500);
            break;
        }
        default:
            break;
    }
};

if (process.env.REACT_APP_ENVIRONMENT !== 'production' && module.hot) {
    module.hot.accept('./reducers', () => {
        // eslint-disable-next-line
        const { getRootReducer } = require('./reducers');
        const rootReducer = getRootReducer(history);

        store.replaceReducer(rootReducer);
    });
}

const castCatalogItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const {
        catalogPage: { screenIndexMap },
        productsData: { itemsMap },
    } = state;

    return {
        ignored_words: ['', ''],
        items: Object.values(itemsMap)
            .filter(({ id }) => screenIndexMap[id])
            .map(({ id, name: title }) => {
                const number = screenIndexMap[id];

                return {
                    id,
                    title,
                    visible: true,
                    number,
                };
            }),
    };
};

const castItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const pathname = selectPathname(state);

    switch (pathname) {
        case ROUTES.CATALOG:
            return castCatalogItemSelectorFromStore(state);

        default:
            return {
                ignored_words: [],
                items: [],
            };
    }
};

const serializeAddress = (storeUser: StoreUser): ScenarioUserAddress => {
    const {
        address,
        city,
        street,
        house,
        building,
        floor,
        entrance,
        apartment,
        doorCode,
        latitude,
        longitude,
    } = storeUser;

    const location: ScenarioUserLocation = { latitude, longitude };

    return {
        location,
        address_string: address,
        city,
        street,
        house,
        building,
        floor,
        entrance,
        apartment,
        door_code: doorCode,
        confirmed: 'true',
    };
};

const prepareAddress = (state: AppState): ScenarioUser => {
    let source;

    switch (selectPathname(state)) {
        case ROUTES.ADDRESS_SIMPLE:
        case ROUTES.ADDRESS_FULL_ALL: {
            source = state.userModel;

            break;
        }

        default: {
            source = state.userData;

            break;
        }
    }

    return {
        phone_num: source.phoneNumber,
        customer_name: source.customerName,
        address: serializeAddress(source),
    };
};

const castFromStore = (): AppStateInterface => {
    const state = store.getState();
    const { cart } = state;
    const pathname = selectPathname(state);

    return {
        item_selector: castItemSelectorFromStore(state),
        foodtech: {
            currentScreen: pathname.substring(1),
            products: Object.keys(cart.itemsMap)
                .map((id) => {
                    const quantity = getCountById(state, id);

                    return { id, quantity };
                })
                .filter(({ quantity }) => Boolean(quantity)),
            totalPrice: {
                value: cart.totalPrice,
                discountValue: cart.totalPrice,
            },
            ...prepareAddress(state),
        },
    };
};

if (window.appInitialData) {
    if (Array.isArray(window.appInitialData)) {
        window.appInitialData.forEach(dispatchToReduxStore);
    } else {
        dispatchToReduxStore(window.appInitialData);
    }
}

/* eslint-disable no-console */
window.AssistantClient = window.AssistantClient || {};

if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
    // eslint-disable-next-line no-underscore-dangle
    window.__inbox = window.__inbox || [];
    // eslint-disable-next-line no-underscore-dangle
    window.__outbox = window.__outbox || [];
}

/**
 * Assistand SDK API implementation:
 */
window.AssistantClient.onStart = () => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke onStart callback');
    }
};

window.AssistantClient.onData = (command: AssistantSdkCommand) => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        // eslint-disable-next-line no-underscore-dangle
        window.__inbox.push(command);
    }

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log(`server command: ${JSON.stringify(command)}`);
    }

    dispatchToReduxStore(command);
};

window.AssistantClient.sendData = (message: AssistantSdkServerAction) => {
    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
        window.AssistantClient.onRequestState();
    }

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log(`sendData: ${JSON.stringify(message)}`);
    }

    if (window.AssistantHost && typeof window.AssistantHost.sendData === 'function') {
        window.AssistantHost.sendData(JSON.stringify(message), null);
    }
};

window.AssistantClient.onRequestState = () => {
    const state = castFromStore();
    const stateString = JSON.stringify(state);

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke request state callback', stateString);
    }

    if (window.AssistantHost && typeof window.AssistantHost.updateState === 'function') {
        window.AssistantHost.updateState(stateString);
    }

    return state;
};

window.AssistantClient.close = () => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('call close native method');
    }

    if (window.AssistantHost && typeof window.AssistantHost.close === 'function') {
        window.AssistantHost.close();
    }
};

if (window.AssistantHost && typeof window.AssistantHost.ready === 'function') {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('call ready native method');
    }

    window.AssistantHost.ready();
}

//     
if (process.env.REACT_APP_ENVIRONMENT === 'development' && !window.location.href.includes('address-simple')) {
    setTimeout(() => {
        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line @typescript-eslint/camelcase
            smart_app_data: GO_TO_CATALOG,
        });
    }, 100);
}
