import React from 'react';
import styled from 'styled-components';
import { color } from 'style/const/color';
import { FootnoteText1 } from 'components/Text/Footnote';

export type LabelType = 'sold-out' | 'few-left' | 'sale' | 'price-changed' | 'you-want';

const contentMap: Record<LabelType, (s?: string) => string> = {
    'sold-out': () => '',
    'few-left': () => ' ',
    'price-changed': () => ' ',
    sale: (value) => ` ${value}%`,
    'you-want': (value) => `  ${value} `,
};

const textColorMap: Record<LabelType, string> = {
    'sold-out': color.text.warning,
    'few-left': color.text.warning,
    'price-changed': color.text.warning,
    sale: color.text.active,
    'you-want': color.text.warning,
};

interface StyledLabelProps {
    type: LabelType;
}
const StyledLabel = styled(FootnoteText1)<StyledLabelProps>`
    color: ${({ type }) => textColorMap[type]};
`;

export interface LabelProps extends React.HTMLAttributes<HTMLDivElement> {
    className?: string;
    type: LabelType;
    content?: string;
}
export const Label: React.FC<LabelProps> = ({ type, content, className }) => {
    const contentProvider = contentMap[type];

    return (
        <StyledLabel className={className} type={type}>
            {contentProvider(content)}
        </StyledLabel>
    );
};
