import { AppReducer } from 'store/constants';

export const waitPageView: AppReducer = (state, action) => {
    switch (action.type) {
        case 'MAKE_PURCHASE': {
            state.waitPage.text = ' ...';
            break;
        }
        case 'VALIDATION_OK': {
            state.waitPage.text = '';
            break;
        }
        case 'ORDER_ERROR': {
            state.waitPage.text = '';
            break;
        }
        default: {
            break;
        }
    }
};
